/* eslint-disable */
declare let AMap: any; // 声明地图

export default class AMapAPI {
  private amap: any;
  private markerClusterer: any;
  private markers: any[] = []; // marker点集合
  private itemList: any[] = []; // 数据源
  private pressMarker: Function; // 点击marker点回调
  private geolocation: any; // 定位对象
  private lastTime = 0; // 记录上次定位时间

  private markerIcon = new AMap.Icon({
    size: new AMap.Size(35, 44),    // 图标尺寸
    image: 'static/icon/marker_ic1.png',  // Icon的图像
    imageSize: new AMap.Size(35, 44),
    imageOffset: new AMap.Pixel(0, 0)
  });
  private markerIcon2 = new AMap.Icon({
    size: new AMap.Size(35, 44),    // 图标尺寸
    image: 'static/icon/marker_ic.png',  // Icon的图像
    imageSize: new AMap.Size(35, 44),
    imageOffset: new AMap.Pixel(0, 0)
  });

  // 地图div组件Id,缩放等级, 点击回调
  constructor(mapId: string, zoom: number = 7, pressMarker: Function) {
    this.pressMarker = pressMarker;

    // 创建地图实例
    this.amap = new AMap.Map(mapId, {
      // viewMode: '3D',
      resizeEnable: true, //是否监控地图容器尺寸变化
      center: [112, 22],
      zoom
    });

    // 定位
    AMap.plugin('AMap.Geolocation', () => {
      const geolocation = new AMap.Geolocation({
        enableHighAccuracy: true,//是否使用高精度定位，默认:true
        timeout: 20000,          //超过10秒后停止定位，默认：5s
        buttonPosition: 'LB',    //定位按钮的停靠位置
        buttonOffset: new AMap.Pixel(16, 24),//定位按钮与设置的停靠位置的偏移量，默认：Pixel(10, 20)
        zoomToAccuracy: true,   //定位成功后是否自动调整地图视野到定位点
      });
      this.amap.addControl(geolocation);
      this.geolocation = geolocation;
      this.getLocation()
    });
  }

  // 移动回调, 缩放回调
  public listener(mCallback: Function, zCallback: Function) {

    //绑定地图移动与缩放事件
    this.amap.on('moveend', () => this.getMapInfo(mCallback));
    this.amap.on('zoomend', () => this.getMapInfo(zCallback));

  }

  // 获取位置
  public getLocation(): Promise<any> {
    return new Promise((resolve) => {
      this.geolocation.getCurrentPosition(function (status: string, result: any) {
        console.log(result)
        if (status === 'complete') {
          console.log(result)
          resolve(result);
        } else {
          resolve({ status: false, message: '获取位置失败' });
        }
      });
    });
  }

  //显示地图层级与中心点信息
  private getMapInfo(onMapChange?: Function) {
    if (Date.now() - this.lastTime > 500) {
      this.lastTime = Date.now();
      let zoom = this.amap.getZoom(); //获取当前地图级别
      let center = this.amap.getCenter(); //获取当前地图中心位置

      if (onMapChange) {
        onMapChange({
          zoom,
          lat: center.lat,
          lng: center.lng,
        })
      }
    }
  }

  // 添加多个marker
  public addMarkers(data: Array<any>) {

    const list = data.map((e: any) => {
      return {
        lnglat: new AMap.LngLat(e.longitude, e.latitude),
        key: e.id + "",
        select: false,
      };
    });
    this.itemList = list;
    this.createMarkers();
  }

  // 创建多个marker
  private createMarkers() {
    if (this.markerClusterer) {
      this.markerClusterer.setMap(null);
      this.markers = [];
    }
    const list = this.itemList;
    for (let i = 0; i < list.length; i++) {
      const point = list[i];
      let marker = this.createMarker(point);
      // 设置点标记的动画效果，此处为弹跳效果
      marker.setAnimation('AMAP_ANIMATION_DROP');
      marker.on('click', (e: any) => this.onPressMarker(i, e));
      this.markers.push(marker)
    }

    this.addCluster();
  }

  // 创建marker点
  private createMarker(point: any) {
    return new AMap.Marker({
      icon: point.select ? this.markerIcon2 : this.markerIcon,
      title: point.key,
      position: point.lnglat,
      // content: `<div></div>`,
      offset: new AMap.Pixel(-16, -22)
    });
  }

  // 添加聚合点
  private addCluster() {
    if (this.markerClusterer) {
      this.markerClusterer.setMap(null);
    }
    this.markerClusterer = new AMap.MarkerClusterer(this.amap, this.markers, {
      gridSize: 80,
      minClusterSize: 6,
      renderClusterMarker: this.createClusterMarker
    });
  }

  // marker点点击事件-待优化
  private onPressMarker(num: number, data: any) {

    this.itemList.forEach((e: any, index: number) => {
      e.select = index === num;
    });

    this.markers.forEach((e, index: number) => {
      if (num === index) {
        e = this.onUpdateMarker(data);
      }
    });
    this.createMarkers();
    if (this.pressMarker) {
      this.pressMarker(1, num);
    }
  }

  // 更新marker点
  private onUpdateMarker(e: any) {
    // let marker = markers[num];
    // amap.remove(marker);
    // marker.setMap(null);

    return new AMap.Marker({
      icon: this.markerIcon,
      title: 'point.key',
      position: new AMap.LngLat(e.lnglat.lng, e.lnglat.lat),
      // content: `<div></div>`,
      offset: new AMap.Pixel(-14, -20)
    });
  }

  // 创建聚合点
  private createClusterMarker(context: any) {
    let size = 88;
    let div = document.createElement('div');
    div.style.backgroundColor = '#C79F62E0';
    div.style.width = '88px';
    div.style.height = '60px';
    div.style.display = 'flex';
    div.style.flexDirection = 'column';
    div.style.alignItems = 'center';
    div.style.justifyContent = 'center';
    div.style.border = 'solid 1px #E5DBC4';
    div.style.borderRadius = '8px';
    // div.style.boxShadow = '0 0 1px #E5DBC4' + shadowColor;
    // div.innerHTML = `<span>上海</span><span style="font-size: 14px">${context.count}家</span>`;
    div.innerHTML = `<span style="font-size: 14px">${context.count}家</span>`;
    div.style.color = '#FFFFFF';
    div.style.fontSize = '16px';
    div.style.textAlign = 'center';
    context.marker.setOffset(new AMap.Pixel(-size / 2, -size / 2));
    context.marker.setContent(div)
  };
}
