/* eslint-disable */
import { request } from '../net/axios';

// 访问本地json文件
export function getJsonData(): Promise<any> {
  return request({
    url: 'static/json/geojson.json',
    method: 'GET',
  });
}

// 获取筛选条件数据
export function queryOption(): Promise<any> {
  return request({
    url: 'parameter/getHomeSearchParameter',
    method: 'GET',
  });
}

// 首页地图数据
export function queryCity(data: object): Promise<any> {
  return request({
    url: 'home/queryElderlyList',
    method: 'GET',
    data
  });
}

// 获取机构列表数据
export function queryOrgList(data: object): Promise<any> {
  return request({
    url: 'home/queryElderlyList/list',
    method: 'POST',
    data
  });
}

// 城市查询
export function queryDistrict(data: string): Promise<any> {
  return request({
    url: 'home/queryDistrict?name=' + data,
    method: 'GET',
    // data
  });
}

// 初始化地图点查询
export function queryOrgMap(data: object): Promise<any> {
  return request({
    url: 'home/queryElderlyMap/condition',
    method: 'POST',
    data
  });
}

// 地图点查询一级-不带详细信息
export function queryMapMarker(data: object): Promise<any> {
  return request({
    url: 'home/queryElderlyMap/lotAndLat',
    method: 'POST',
    data
  });
}

// 地图点查询二级-带详细信息
export function queryMarkers(data: object): Promise<any> {
  return request({
    url: 'home/queryElderlyMap/detailList',
    method: 'POST',
    data
  });
}
